// todo ---------------------------- Style -------------------------------------
.wrapper {
  min-height: 100vh;
  height: 100%;
}

[class*='_container'] {
  position: relative;
  max-width: $maxWidthContainer;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 2rem;

  @include screen-1140 {
    padding: 0 2rem;
  }
}

[class*='icon-'] {
  cursor: pointer;
  color: inherit;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

[class*='_img'] {
  pointer-events: none;
  height: 100%;

  img {
    vertical-align: middle;
    object-fit: cover;
  }
}

[class*='_title'] {
  h2,
  h3,
  h4,
  h5 {
    font-weight: 600;
    line-height: 100%;
    transform: scaleY(1.3);

    span {
      filter: contrast(150%);
      letter-spacing: 1.1px;
    }
  }

  h2,
  h3,
  h4 {
    text-transform: uppercase;
  }
}
