@use "all" as *;

@font-face {
  font-family: "#{$icon-font-family}";
  src:
    url("#{$icon-font-path}/#{$icon-font-family}.ttf?9njddx") format("truetype"),
    url("#{$icon-font-path}/#{$icon-font-family}.woff?9njddx") format("woff"),
    url("#{$icon-font-path}/#{$icon-font-family}.svg?9njddx##{$icon-font-family}")
      format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"]::before,
[class*=" icon-"]::before {
  font-family: "#{$icon-font-family}" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-services {
  &:before {
    content: $icon-services;
  }
}
