@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+3:ital,wght@0,200..900;1,200..900&display=swap");
:root {
  --background-color-transparent: rgba(19, 19, 22, 0.79);
  --font-body-color: #ffefcd;
  --font-color-second: #424539;
  --font-color-title: rgb(136, 192, 255);
  --font-color-active: rgb(119, 150, 243);
  --font-color-not-active: rgba(139, 176, 255, 0.424);
  --background-color: rgb(248, 245, 255);
  --background-second: rgb(2, 10, 16);
  --background-selected: rgba(43, 45, 65, 0.337);
  --selected-color: rgb(113, 203, 255);
  --themes-color: rgb(94, 77, 168);
  --background-thumb: rgb(22, 22, 30);
  --scrollbar-thumb-bgcolor: rgb(71, 83, 45);
  --border-color: rgba(60, 84, 141, 0.286);
  --color-active: rgb(4, 217, 255);
  --alarm-color: rgba(232, 135, 190, 0.6);
  --pos: center center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: rgb(59, 134, 134);
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

/**
 * Swiper 11.2.8 
 */
:root {
  --swiper-scrollbar-top: auto;
  --swiper-scrollbar-bg-color: rgba(0, 0, 0, 0.1);
  --swiper-scrollbar-drag-bg-color: rgba(0, 0, 0, 0.5);
  --swiper-scrollbar-size: 0.4rem;
  --swiper-scrollbar-bottom: 0.4rem;
  --swiper-scrollbar-sides-offset: 1%;
  --swiper-scrollbar-border-radius: 1rem;
  --swiper-theme-color: $themeColor;
  --swiper-navigation-size: 3rem;
  --swiper-pagination-color: #f65e38;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-size: 1.2rem;
  --swiper-pagination-bullet-border-radius: 50%;
  --swiper-pagination-bullet-inactive-opacity: 0.4;
  --swiper-pagination-bullet-inactive-color: rgb(0, 0, 0);
  --swiper-pagination-bullet-width: 1.2rem;
  --swiper-pagination-bullet-height: 1.2rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-bottom: 0.5rem;
  --swiper-pagination-bullet-horizontal-gap: 0.5rem;
  --swiper-transition-opacity: 0.9s;
}

.top-box {
  align-items: center;
}
@media (min-width: 960.99px) {
  .top-box {
    min-height: 100vh;
  }
}

.link_split {
  position: relative;
  outline: 0;
  line-height: 1;
  transition: color 0s 0.5s;
  display: inline-block;
  white-space: nowrap;
}
.link_split:hover {
  text-shadow: none;
  color: transparent;
  transition: none;
}
.link_split:hover::before {
  transform: translate3d(0px, -2px, 0);
}
.link_split:hover::after {
  transform: translate3d(0px, 2px, 0);
}
.link_split:hover::before, .link_split:hover::after {
  color: #211041;
  transition: color 0.5s, transform 0.5s;
}
.link_split::before, .link_split::after {
  content: attr(data-letters);
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  transition: color 0.5s, transform 0.5s;
}
.link_split::before {
  clip-path: polygon(0 0, 100% 0%, 100% 30%, 0 65%);
}
.link_split::after {
  clip-path: polygon(0 65%, 100% 30%, 100% 100%, 0 100%);
}

.link_underline-center {
  position: relative;
  transition: color 0.3s ease-in-out;
}
.link_underline-center::before {
  content: "";
  transform: scaleX(0);
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  transition: transform 0.3s ease-in-out;
  opacity: 0.6;
  background-color: #211041;
}
.link_underline-center::after {
  bottom: 0;
  left: 0;
}
.link_underline-center:hover::before {
  transform: scaleX(1);
}
.link_underline-center:hover a {
  opacity: 1;
}

.link_underline::before, .link_underline::after {
  position: absolute;
  width: 0;
  height: 1px;
  content: "";
  transition: width 0.4s ease-in-out;
  opacity: 0.6;
  background-color: #211041;
}
.link_underline::before {
  right: 0;
  bottom: 0;
}
.link_underline::after {
  bottom: 0;
  left: 0;
}
.link_underline._active::before {
  width: 45%;
}
.link_underline._active::after {
  width: 45%;
}
.link_underline:hover {
  transition: color 0.4s ease-in-out;
  color: #211041;
}
.link_underline:hover::before {
  width: 45%;
}
.link_underline:hover::after {
  width: 45%;
}
.link_underline:hover a {
  opacity: 1;
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.transform-perspective {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  perspective: 100px;
}
.transform-perspective .transform-box {
  transition: all 0.8s cubic-bezier(0.77, 0, 0.175, 1);
}
.transform-perspective .transform-box:hover {
  opacity: 0.3 !important;
  transform-origin: 50% -100%;
  transform: rotateX(-16deg) translateZ(-22px);
}

.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;
  /* Размер фона для каждого слоя */
  background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  /* Позиционирование фона */
  background-repeat: no-repeat;
  /* Повторение фона */
  background-position: 100% 100%, 0 100%; /* Второе изображение */
  /* Переход для изменения размера фона */
  background-size: 100% 1px, 0 1px; /* Второе изображение */
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 6rem;
}
.pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #2a3514;
  transition: background 0.5s ease-in-out;
}
.pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 2rem;
}
.pagination .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #9acd32;
  scale: 1.2;
  transition: scale 0.5s ease-in-out;
}
.pagination.swiper-pagination-lock {
  display: none;
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Source Sans 3", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
  content: "";
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 12px solid #e7e9eb;
  position: absolute;
  right: -12px;
  top: 0;
  z-index: 1;
}

html {
  font-style: normal;
  font-size: 62.5%;
  font-weight: 400;
  font-family: "Source Sans 3", sans-serif;
  color: #48386b;
  height: 100%;
  counter-reset: heading;
}
@media screen and (max-width: 1024px) {
  html {
    font-size: 50%;
  }
}

body {
  overscroll-behavior: none;
  font: 1.6rem/1.6 "Source Sans 3", sans-serif;
  background-color: rgb(241, 234, 255);
}
body.no-scroll {
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
  scrollbar-width: none;
  -webkit-tap-highlight-color: transparent;
}

img,
svg {
  user-select: none;
  width: 100%;
  height: 100%;
  max-width: 100%;
  aspect-ratio: auto;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  vertical-align: baseline;
}

a:focus {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

button,
input[type=submit] {
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input,
label,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
  border: none;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

::placeholder {
  /* Most modern browsers support this now. */
  color: #7e6aa8;
}

@font-face {
  font-family: "icon-set";
  font-display: swap;
  src: url("../fonts/icon-set.eot") format("eot"), url("../fonts/icon-set.woff") format("woff"), url("../fonts/icon-set.woff2") format("woff2"), url("../fonts/icon-set.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}
:root {
  --swiper-theme-color: $themeColor;
  /*
   --swiper-preloader-color: var(--swiper-theme-color);
   --swiper-wrapper-transition-timing-function: initial;
   */
}

:host {
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  z-index: 1;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
  display: block;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  transition-timing-function: var(--swiper-wrapper-transition-timing-function, initial);
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-ios .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-horizontal {
  touch-action: pan-y;
}

.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: auto;
  position: relative;
  transition-property: transform;
  display: block;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}

.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* 3D Effects */
.swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}

.swiper-3d .swiper-wrapper {
  transform-style: preserve-3d;
}

.swiper-3d {
  perspective: 1200px;
}
.swiper-3d .swiper-slide,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

/* CSS Mode */
.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none; /* For Firefox */
  -ms-overflow-style: none; /* For Internet Explorer and Edge */
}
.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}
.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}
.swiper-css-mode.swiper-horizontal > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}
.swiper-css-mode.swiper-vertical > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}
.swiper-css-mode.swiper-free-mode > .swiper-wrapper {
  scroll-snap-type: none;
}
.swiper-css-mode.swiper-free-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: none;
}
.swiper-css-mode.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}
.swiper-css-mode.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
  scroll-snap-stop: always;
}
.swiper-css-mode.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}
.swiper-css-mode.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  min-height: 1px;
  width: var(--swiper-centered-offset-after);
}
.swiper-css-mode.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}
.swiper-css-mode.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  min-width: 1px;
  height: var(--swiper-centered-offset-after);
}

/* Slide styles start */
/* 3D Shadows */
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}
.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}
.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-lazy-preloader {
  width: 42px;
  height: 42px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -21px;
  margin-top: -21px;
  z-index: 10;
  transform-origin: 50%;
  box-sizing: border-box;
  border: 4px solid var(--swiper-preloader-color, var(--swiper-theme-color));
  border-radius: 50%;
  border-top-color: transparent;
}

.swiper:not(.swiper-watch-progress) .swiper-lazy-preloader,
.swiper-watch-progress .swiper-slide-visible .swiper-lazy-preloader {
  animation: swiper-preloader-spin 1s infinite linear;
}

.swiper-lazy-preloader-white {
  --swiper-preloader-color: #fff;
}

.swiper-lazy-preloader-black {
  --swiper-preloader-color: #000;
}

@keyframes swiper-preloader-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Slide styles end */
.preloader {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  min-height: 100vh;
  z-index: 19;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  background: #48386b;
  transition: opacity 0.7s ease-in-out;
}
.preloader__wr-loader {
  position: absolute;
  top: 55%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
.preloader__loader {
  position: relative;
  width: 1em;
  height: 1em;
  color: rgb(232, 135, 190);
  font-size: 90px;
  text-indent: -9999em;
  overflow: hidden;
  border-radius: 50%;
  margin: 72px auto;
  transform: translateZ(0);
  animation: load 1.7s infinite ease, round 1.7s infinite ease;
}
.preloader__content {
  position: relative;
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.preloader__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 4rem;
  position: absolute;
  z-index: 1;
  min-height: 100vh;
  padding: 8rem 2rem;
}
.preloader__title span {
  display: block;
  font-size: clamp(1.4375rem, 1.2427125rem + 10.051875vw, 12.489375rem);
}
.preloader__title span:nth-child(1), .preloader__title span:nth-child(2) {
  animation: anime-title 3s cubic-bezier(0.22, 0.61, 0.36, 1) infinite;
  text-shadow: 7px 7px 15px #030303;
}
.preloader__title span:nth-child(1) {
  position: relative;
  -webkit-text-stroke: 2px rgb(232, 135, 190);
  display: flex;
  justify-content: center;
}
.preloader__title span:nth-child(2) {
  -webkit-text-stroke: 2px rgb(232, 135, 190);
  animation: anime-title-r 3s cubic-bezier(0.22, 0.61, 0.36, 1) infinite;
}
.preloader.preloader-remove {
  opacity: 0;
}

@keyframes anime-title-r {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(50%);
  }
  100% {
    transform: translateY(0%);
  }
}
@keyframes anime-title {
  0% {
    transform: translateY(50%);
  }
  50% {
    transform: translateY(0%);
  }
  100% {
    transform: translateY(50%);
  }
}
@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes load {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%, 95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%, 59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
