@use '../core/variables' as *;
@use '../core/mixins' as *;

.footer {
  // .footer__body
  &__body {
    padding-block: 8rem 6rem;
  }

  // .footer__content
  &__content {
    color: $background-color;
  }

  // .footer__top
  &__top {
    padding-bottom: 3rem;
    display: grid;
    grid-template-columns: max-content 15% 1fr 1fr min-content;
    justify-content: space-between;
    row-gap: 6rem;

    .footer__column {
      position: relative;
      padding-right: 2rem;
      border-right: 1px solid $border-color;

      &:not(:first-child) {
        padding-left: 2rem;
      }

      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100.5%;
        height: 1px;
        background-color: $border-color;
        margin-bottom: -3rem;
      }

      &:last-child {
        .footer__title {
          text-align: right;
        }
      }

      &:nth-child(5) {
        border-right: none;
      }

      @include screen-1140 {
        &:nth-child(3) {
          grid-row: 2 / 3;
          grid-column: 1 / 3;
          padding-left: 0;
        }

        &:nth-child(4) {
          grid-column: 3 / 4;
          grid-row: 1/ 2;
          border: none;
        }

        &:nth-child(5) {
          text-align-last: left;
          grid-column: 3 / 4;
          grid-row: 2 / 3;
        }

        .footer__line {
          justify-content: flex-start;
        }
      }

      @include screen-580 {
        &:nth-child(2) {
          border: none;
        }

        &:nth-child(3) {
          border: none;
        }

        &:nth-child(4) {
          grid-column: 1 / 2;
          grid-row: 3 / 4;
          border-right: 1px solid $border-color;
          padding-left: 0;
        }

        &:nth-child(5) {
          grid-column: 2 / 3;
          grid-row: 3 / 4;
        }
      }

      @include screen-425 {
        &:nth-child(4) {
          grid-column: 1 / 3 !important;
          grid-row: 3 / 4 !important;
          border: none !important;
        }

        &:nth-child(5) {
          grid-column: 1 / 3 !important;
          grid-row: 4 / 5 !important;
          padding-left: 0;
        }
      }
    }

    @include screen-1140 {
      grid-template-columns: max-content 1fr 1fr;
    }

    @include screen-580 {
      grid-template-columns: 1fr 1fr;
    }
  }

  // .footer__info
  &__info {
    padding-left: 1rem;

    p {
      font-weight: 300;
    }
  }

  // .footer__column
  &__column {
    width: 100%;
    line-height: 140%;
    font-size: 1.6rem;
  }

  // .footer__title
  &__title {
    margin-bottom: 1.4rem;
    white-space: nowrap;

    h5 {
      text-align-last: left;
    }
  }

  // .footer__menu
  &__menu {
    li {
      font-size: 1.6rem;
    }
  }

  // .footer__text
  &__text {
    p {
      font-size: 1.6rem;
    }
  }

  // .footer__logo
  &__logo {
    display: block;
    cursor: pointer;
    margin-top: -13%;

    img {
      pointer-events: none;
      width: 18rem;
      height: 8rem;
      filter: drop-shadow(1px 1px 1px rgba(255, 255, 255, 0.836));

      @include screen-375 {
        max-width: 100%;
      }
    }
  }

  // .footer__contacts
  &__contacts {
    white-space: nowrap;

    a {
      display: block;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }

    ._icon {
      margin-right: 1rem;
      font-size: 2rem;
    }

    .icon-phone-ringing {
      font-size: 2.8rem;
    }
  }

  // .footer__iten
  &__item {
    display: flex;
    align-items: center;

    a {
      display: inline;
      cursor: pointer;
    }
  }

  // .footer__link-key
  &__link-key {
    font-size: 1.6rem;
    &.link_underline-center {
      &::before {
        background-color: #fff;
      }
    }
  }

  // .footer__link
  &__link {
    font-size: inherit;
  }

  // .footer__line
  &__line {
    display: flex;
    justify-content: flex-end;
    gap: 2rem;
  }

  // .footer__icon
  &__icon {
    ._icon {
      font-size: 3rem;
    }
  }

  // .footer__bottom
  &__bottom {
    padding-top: 3rem;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 14px;
  }

  // .footer__politics
  &__politics {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 2rem;

    a {
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }

    @include screen-490 {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  // .footer__info-col
  &__info-col {
    font-size: 12px;
  }
}
